body {
  min-width: 320px;
  margin: 0;
}

$mobile: 320px;
$tablet: 768px;
$desktop: 1440px;

.container {
  max-width: 280px;
  margin: 0 auto;
}

@media (min-width: $tablet) {
  .container {
    max-width: 708px;
    margin: 0 auto;
  }
}

@media (min-width: $desktop) {
  .container {
    max-width: 1220px;
    margin: 0 auto;
  }
}

@media (min-width: $mobile) {
  br {
    display: none;
  }
}

@media (min-width: $tablet) {
  br {
    display: none;
  }
}

