@media (min-width: $tablet) {
  .textarea {
    margin-bottom: -10px;
  }
}

@media (min-width: $desktop) {
  .textarea {
    margin-bottom: 68px;
  }
}

.textarea__input {
  display: block;
  width: 251px;
  height: 114px;
  border: 1px solid $color-achromatic-extra-2;
  padding: 11px 12px 23px 15px;
  font-family: $font-main-regular;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0;
  text-align: left;
  text-transform: uppercase;
  color: $color-achromatic-2;
}

@media (min-width: $tablet) {
  .textarea__input {
    width: 601px;
    height: 140px;
    margin-right: 0;
    margin-left: 41px;
    border: 1px solid $color-achromatic-extra-2;
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: $desktop) {
  .textarea__input {
    width: 100%;
    height: 169px;
    margin-right: 0;
    margin-left: 0;
  }
}
